.item-cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 1000px;
    padding: 5px 20px;
    background-color: rgba(234, 244, 255, 0.87);
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.1);
    .item-content {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 180px;
        height: 100%;
        padding-left: 20px;
        .item-holder {
            width: 100%;
            .content {
                padding: 0;
                margin: 0;
                font-size: 0.8rem;
                color: rgba(17, 34, 53, 0.87);
                font-weight: 600;
            }
        }
      }
}
